<template>
  <div
    class="blog-wrapper"
    :class="{'no-user' : !$store.state.user}"
  >
    <div class="blog-content">
      <div>
        <h2 v-if="post.welcomeScreen">{{ post.title }}</h2>
        <h2 v-else>{{ post.title }}</h2>
        <!-- <h2 v-else>{{ post.blogTitle }}</h2> -->
        <p v-if="post.welcomeScreen">{{ post.blogPost }}</p>
        <p
          class="content-preview"
          v-else
        >{{ post.blogHTML }}</p>
        <!-- <p class="content-preview" v-else v-html="post.blogHTML"</p> -->
        <router-link
          v-if="post.welcomeScreen"
          class="link link-light"
          to="#"
        >
          <Arrow class="arrow arrow-light" />登录/注册
        </router-link>
        <router-link
          v-else
          class="link"
          :to="{name:'ViewBlog',params:{blogid:this.post.blogID}}"
        >
          <Arrow class="arrow" />查看文章
        </router-link>
      </div>
    </div>
    <div class="blog-photo">
      <img
        v-if="post.welcomeScreen"
        :src="require(`../assets/blogPhotos/${post.photo}.jpg`)"
        alt=""
      />
      <img
        v-else
        :src="require(`../assets/blogPhotos/${post.blogCoverPhoto}.jpg`)"
        alt=""
      />
      <!-- <img
        v-else
        :src="post.blogCoverPhoto"
        alt=""
      /> -->
    </div>
  </div>
</template>

<script>
import Arrow from "../assets/Icons/arrow-right-light.svg";
export default {
  name: "blogPost",
  props: ["post"],
  components: {
    Arrow,
  },
};
</script>

<style lang="scss" scope>
.blog-wrapper {
  display: flex;
  flex-direction: column;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  @media (min-width: 700px) {
    max-height: 650px;
    min-height: 600px;
    flex-direction: row;
  }
  .blog-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 4;
    order: 2;
    @media (min-width: 700px) {
      order: 1;
    }
    @media (min-width: 800px) {
      flex: 3;
    }
    div {
      max-width: 375px;
      padding: 72px 24px;
    }
    h2 {
      font-size: 32px;
      font-weight: 300;
      text-transform: uppercase;
      margin-bottom: 24px;
      @media (min-width: 700px) {
        font-size: 40px;
      }
    }
    p {
      font-size: 15px;
      font-weight: 300;
      line-height: 1.7;
    }
    .content-preivew {
      font-size: 13px;
      height: 24px;
      white-space: nowrap;
      text-overflow: ellipsis;
      width: 100px;
    }
    .link {
      display: inline-flex;
      align-items: center;
      margin-top: 32px;
      padding-bottom: 4px;
      transition: 0.5s ease-in all;
      border-bottom: 1px solid transparent;
      &:hover {
        border-bottom-color: #303030;
      }
    }
    .link-light {
      &:hover {
        border-bottom-color: #ffff;
      }
    }
  }
  .blog-photo {
    order: 1;
    flex: 3;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
      0 2px 4px -1px rgba(0, 0, 0, 0.06);
    @media (min-width: 700px) {
      order: 2;
    }
    @media (min-width: 800px) {
      flex: 4;
    }
    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &:nth-child(even) {
    .blog-content {
      order: 2;
    }
    .blog-photo {
      order: 1;
    }
  }
}
.no-user:first-child {
  .blog-content {
    background-color: #303030;
    color: #fff;

    .link {
      color: #fff;
    }
  }
}

.arrow {
  margin-right: 8px;
  width: 12px;
}

.arrow-light {
  path {
    fill: #fff;
  }
}
</style>